
import Tik from './Tik.PNG';
import clock from './clock.png';
import game from './game.png';
import expense from './expense.png';
import Calculator from './Calculator.png';
import vowel from './vowel.png';
import todo from './todo.png';
import React, { useState } from 'react';
import {useTransition, animated} from 'react-spring';

export default function Projects() {
    const [items, set] = useState([{'text': ""}])
    const transitions = useTransition(items, item => item.key, {
        from: { transform: 'translate3d(0,-100px,0)' },
        enter: { transform: 'translate3d(0,0px,0)' },
        leave: { transform: 'translate3d(0,-100px,0)' },
        })

    return (transitions.map(({ item, props, key }) =>
    <animated.div key={key} style={props}>
        <div className="Projects">
            <div className="project1">
                <a href="https://dhanapalandp.github.io/expensetracker/" target="_blank"><img src={expense}  className="projectimg4"/></a>
                <p className="projectName" style={{textAlign: "center", fontWeight: "600"}}>Expense Tracker</p>
            </div>
            <div className="project2">
                <a href="https://dhanapalandp.github.io/Todoapp/" target="_blank"><img src={todo}  className="projectimg2"/></a>
                <p className="projectName" style={{textAlign: "center", fontWeight: "600"}}>Todo List</p>
            </div>
            <div className="project3">
                <a href="https://dhanapalandp.github.io/flipgame/" target="_blank"><img src={game}  className="projectimg3"/></a>
                <p className="projectName" style={{textAlign: "center", fontWeight: "600"}}>Flip game</p>
            </div>
            <div className="project4">
                <a href="https://dhanapalandp.github.io/vowelcounter/" target="_blank"><img src={vowel} className="projectimg1"/></a>
                <p  className="projectName" style={{textAlign: "center", fontWeight: "600"}}>Vowel counter</p>
            </div>
            <div className="project5">
                <a href="https://dhanapalandp.github.io/clock/" target="_blank"><img src={clock}  className="projectimg5"/></a>
                <p className="projectName" style={{textAlign: "center", fontWeight: "600"}}>Clock</p>
            </div>
            <div className="project6">
                <a href="https://dhanapalandp.github.io/calculator/" target="_blank"><img src={Calculator}  className="projectimg6"/></a>
                <p className="projectName" style={{textAlign: "center", fontWeight: "600"}}>Calculator</p>
            </div>
        </div></animated.div>
    )
    )
}
